___CSS_LOADER_EXPORT___.push([module.id, ".tr-selected{background-color:rgba(0,0,255,.2);border:1px dashed #444;color:blue;font-weight:600}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Inventario/Components/RegistrosComponent.vue"],"names":[],"mappings":"AAoLA,aACA,iCAAA,CACA,sBAAA,CACA,UAAA,CACA,eACA","sourcesContent":["<template>\r\n    <v-dialog v-model=\"dialog\" fullscreen scrollable>\r\n        <template v-slot:activator=\"{ on, attrs }\">\r\n            <v-btn block v-bind=\"attrs\" v-on=\"on\">\r\n                Mis Registros <v-icon right>mdi-format-list-checks</v-icon>\r\n            </v-btn>\r\n        </template>\r\n        <v-card tile>\r\n            <v-toolbar>\r\n                <v-app-bar-nav-icon @click=\"dialog = false\">\r\n                    <v-icon>mdi-arrow-left</v-icon>\r\n                </v-app-bar-nav-icon>\r\n\r\n                <v-toolbar-title>Bienes Registrados</v-toolbar-title>\r\n            </v-toolbar>\r\n\r\n            <div class=\"text-center text-primary\">\r\n                <small class=\"primary--text\">*Doble toque para seleccionar</small>\r\n            </div>\r\n\r\n\r\n            <v-card-text style=\"height: 90vh;\">\r\n                <v-card tile>\r\n                    <v-overlay absolute :value=\"loading_table\">\r\n                        <v-progress-circular indeterminate size=\"64\"></v-progress-circular>\r\n                    </v-overlay>\r\n\r\n                    <v-row class=\"pa-4\">\r\n                        <v-col cols=\"12\" md=\"10\" class=\"mb-1\">\r\n                            <v-text-field v-model=\"term\" hide-details=\"auto\" dense label=\"Buscar\"\r\n                                placeholder=\"Codigo, descripciÃ³n, oficina \" outlined>\r\n                            </v-text-field>\r\n                        </v-col>\r\n                        <v-col cols=\"12\" md=\"2\" class=\"mb-1\">\r\n                            <v-text-field v-model=\"correlativo\" hide-details=\"auto\" dense label=\"Correlativo\"\r\n                                placeholder=\"Correlativo\" outlined>\r\n                            </v-text-field>\r\n                        </v-col>\r\n\r\n                    </v-row>\r\n\r\n                    <v-divider></v-divider>\r\n\r\n                    <v-simple-table>\r\n                        <template v-slot:default>\r\n                            <thead class=\"grey lighten-3\">\r\n                                <tr>\r\n                                    <th class=\"text-left\">Inventario</th>\r\n                                    <th class=\"text-left\">Codigo</th>\r\n                                    <th class=\"text-left\">Descripcion</th>\r\n                                    <th class=\"text-left\">Dependencia</th>\r\n                                    <th class=\"text-left\">Sticker</th>\r\n                                </tr>\r\n                            </thead>\r\n                            <tbody>\r\n                                <tr v-for=\"(item, index) in bienes_result\" :key=\"index\"\r\n                                    @dblclick=\"onSelectColumDobleClik(item)\">\r\n                                    <td>\r\n                                        <template v-if=\"item.idbienk\">\r\n                                            {{ item.idbienk }}\r\n                                        </template>\r\n                                        <template v-else>\r\n                                            <v-chip small class=\"ma-2\" color=\"green\" text-color=\"white\">\r\n                                                Nuevo\r\n                                            </v-chip>\r\n                                        </template>\r\n                                    </td>\r\n                                    <td>{{ item.codigo }}</td>\r\n                                    <td>{{ item.descripcion }}</td>\r\n\r\n                                    <td>\r\n                                        <strong>{{ item.dependencia }}</strong>\r\n                                        -\r\n                                        {{ item.nombre }}\r\n                                    </td>\r\n\r\n                                    <td>\r\n                                        {{ item.corr_area }}\r\n                                        -\r\n                                        {{ item.corr_num }}\r\n                                    </td>\r\n                                </tr>\r\n                            </tbody>\r\n                        </template>\r\n                    </v-simple-table>\r\n                    <v-card-actions>\r\n                        <v-spacer></v-spacer>\r\n                        <v-pagination v-model=\"page\" class=\"\" :length=\"pages\" :total-visible=\"5\"></v-pagination>\r\n                        <v-spacer></v-spacer>\r\n                    </v-card-actions>\r\n                </v-card>\r\n            </v-card-text>\r\n        </v-card>\r\n    </v-dialog>\r\n</template>\r\n<script>\r\nimport axios from \"axios\";\r\nexport default {\r\n    props: {\r\n        areas: Array,\r\n    },\r\n    data: () => ({\r\n        loading_table: false,\r\n        dialog: false,\r\n        bienes_result: [],\r\n        page: 1,\r\n        total_result: 0,\r\n        pages: 1,\r\n        term: '',\r\n        correlativo: '',\r\n        page_temp: 1,\r\n\r\n    }),\r\n    async created() {\r\n        this.loading_table = true;\r\n        this.bienes_result = await this.getBienes();\r\n        this.loading_table = false;\r\n    },\r\n\r\n    methods: {\r\n        async getBienes() {\r\n\r\n            let res = await axios.post(\r\n                \"/inventario/get-bienes-usuario?page=\" + this.page_aux, {\r\n                term: this.term,\r\n                correlativo: this.correlativo\r\n            }\r\n            );\r\n\r\n            this.page = res.data.datos.current_page;\r\n            this.total_result = res.data.datos.total;\r\n            this.pages = res.data.datos.last_page;\r\n            return res.data.datos.data;\r\n        },\r\n\r\n        async onSelectColumDobleClik(item) {\r\n            item.registrado = true;\r\n            item.is_inventario = true;\r\n            this.$emit(\"setData\", item);\r\n            this.dialog = false;\r\n        },\r\n    },\r\n    watch: {\r\n        async page(val, old_val) {\r\n            if (val == old_val) return;\r\n            //if (val == old_val) return;\r\n            this.loading_table = true;\r\n            this.page_aux = val;\r\n            let res = await this.getBienes();\r\n            this.bienes_result = res;\r\n            this.loading_table = false;\r\n        },\r\n\r\n        async term(val, old_val) {\r\n            if (val == old_val) return;\r\n            this.loading_table = true;\r\n            this.page_aux = 1;\r\n            let res = await this.getBienes();\r\n            this.bienes_result = res;\r\n            this.loading_table = false;\r\n        },\r\n\r\n        async correlativo(val, old_val) {\r\n            if (val == old_val) return;\r\n            this.loading_table = true;\r\n            this.page_aux = 1;\r\n            let res = await this.getBienes();\r\n            this.bienes_result = res;\r\n            this.loading_table = false;\r\n        },\r\n\r\n        async dialog(val) {\r\n            if (val == false) return;\r\n            this.bienes_result = await this.getBienes();\r\n        },\r\n    },\r\n};\r\n</script>\r\n\r\n<style>\r\n.tr-selected {\r\n    background-color: rgba(0, 0, 255, 0.2);\r\n    border: 1px dashed #444;\r\n    color: blue;\r\n    font-weight: 600;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
